.footer {
    background-color: $color-dark-gray-4;
    padding: 10rem 0;
    font-size: $default-font-size;
    color: $color-light-gray-1;

    &__logo-box {
        text-align: center;
        margin-bottom: 8rem;
    }

    &__logo {
        width: 15rem;
        height: auto;
    }


    &__navigation {
        border-top: 1px solid $color-light-gray-2;
        padding-top: 2rem;
        display: inline-block;
    }


    &__list {
        list-style: none;
    }

    &__item {
        display: inline-block;

        &:not(:last-child) {
            margin-right: 1.5rem;
        }
    }

    &__link {

        &:link,
        &:visited {
            color: $color-light-gray-1;
            background-color: $color-dark-gray-4;
            text-decoration: none;
            display: inline-block;
            text-transform: uppercase;
            transition: all 0.2s;
        }

        &:hover {
            color: $color-primary;
            box-shadow: 0 1rem 2rem rgba($color: #000000, $alpha: .3);
            transform: rotate(5deg) scale(1.2);
        }
    }

    &__copyright {
        border-top: 1px solid $color-light-gray-1;
        padding-top: 2rem;
        width: 80%;
        float: left;

    }
}